import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

// const originalPush = Router.prototype.push

// Router.prototype.push = function (location) {
//   return originalPush.call(this, location).catch(err => err)
// }

export default new Router({
  routes: [{
      path: "/",
      name: "Home",
      component: () => import("../pages/home/Home.vue")

    },
    {
      path: '/Stories',
      name: "Stories",
      component: () => import("../pages/stories/Stories.vue")
    },
    {
      path: "/About",
      name: "About",
      component: () => import("../pages/about/About.vue")
    },
    {
      path: "/Past",
      name: "Past",
      component: () => import("../pages/past/Past.vue")
    },
    {
      path: "/Team",
      name: "Team",
      component: () => import("../pages/team/Team.vue")
    },
    {
      path: "/Contact",
      name: "Contact",
      component: () => import("../pages/contact/Contact.vue")
    },  {
      path: "/Works",
      name: "Works",
      component: () => import("../pages/works/Works.vue")
    }, {
      path:"/Works/:id",
      name: "Works_Content",
      component: () => import("../pages/works/works_content/Works_Content.vue")
    }
  ]
})



